import { Component } from '@angular/core';
import { responce } from './mock-responce';
import { ResponseItem } from './models/response-item.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  public items: ResponseItem[];
  public typeSort: string;
  public sortWords: string;

  public handleFetch(): void {
    this.items = responce.items;
  }

  public handleSort([type, sortWords]: string[]): void {
    this.typeSort = type;
    this.sortWords = sortWords;
  }
}
